      <div v-if="searchHistory.length>0">
        <div class="home-title">
          历史搜索<span class="btn" id="historyclear" @click="removeAllSearchHistory">清空</span>
        </div>
        <div id="historykey" class="key-group">
          <a class="key-list historykey" v-for="(item,index) in searchHistory" :key="index" @click="searchHandler(item.menu,item.searchValue)"><span>{{item.searchValue.str}}</span></a>
