<script>
import { myMessage } from '@/utils/message'
export default {
  props: {
    songList:{
       type:Array,
    },
    searchHandler:{
      type:Function,
        default: () => {
        return Function;
      }
    }

  },
    data() {
    return {
        searchHistory:[],
        searchValue:{
            str:'',
            pn:0,
            rn:10,
        },
        options: [{
          value: 1,
          label: '默认搜索'
        },
        {
          value: 3,
          label: '酷狗音乐'
        },
        {
          value: 4,
          label: '咪咕音乐'
        },
        {
          value: 5,
          label: '网易云音乐'
        }],
        value: 1,
      }
    },
    methods:{
      //搜索框搜索
      search(){
        this.searchValue.str = this.searchValue.str.trim()
        if (this.searchValue.str) {
          if (this.searchValue.str == "周杰伦") {
            myMessage({
              message:"暂不支持播放付费歌曲",
              type:"warning",
            });  
            this.searchValue = {str:'',pn:0,rn:10,}
            return
          }
          let obj = Object.assign({},this.searchValue);
          this.searchHandler(this.value,obj)

          // 将搜索记录保存到本地
          this.addSearchHistory(obj)
          //初始化搜索框
          this.searchValue = {str:'',pn:0,rn:10,}
        }else{
          myMessage({
            message:"搜索不能为空",
            type:"warning",
          });
        }
      },

      //将搜索记录添加到本地
      addSearchHistory(obj){
        let flag = true;
          this.searchHistory.forEach(item=>{
            if (item.searchValue.str === obj.str) {
              flag = false
              return 
            }
          })
          if (flag){
            this.searchHistory.push({menu:this.value,searchValue:obj})
            localStorage.setItem("searchHistory",JSON.stringify(this.searchHistory))
          }
      },

      removeAllSearchHistory(){
        this.searchHistory = []
        localStorage.removeItem('searchHistory');
      },

      // 查询推荐列表
      searchList(){
        let obj = Object.assign({},{str:93,pn:0,rn:10})
        this.searchHandler(0,obj)
      },

      // 查询推荐列表中的歌
      searchListInfo(index){
        let value =  this.songList[index].id
        let obj = Object.assign({},{str:value,pn:0,rn:10})
        this.searchHandler(2,obj)

      }
    },
    created(){
        this.searchHistory = []
        let value = JSON.parse(localStorage.getItem("searchHistory"));
        if (value) {
          for (const item in value) {
              this.searchHistory.push(value[item])
          }
       }
       console.log(this.searchHistory)
       
    }
};
</script>

