    </div>
    <popup-window v-show="open" :isOpen="isOpen" :musicInfo="musicInfo"></popup-window>
    <div class="modal-backdrop fade show" v-show="open"></div>
  </div>
</template>

<script>
import HomePage from './views/HomePage/HomePage'
import PopupWindow from './components/PopupWindow/PopupWindow.vue'

export default {
  name: 'App',
  components: {
    HomePage,
    PopupWindow
  },
  data(){
    return{
      open:false,
      musicInfo:{},
    }
  },
  methods: {
      isOpen(isOpen,value){
        this.open = isOpen
        this.musicInfo = value
      }
  },
}
</script>

