import request from '@/utils/request'

// 模糊搜索
export function getKbyKeyword(params) {
  return request({
    url: '/v2/music/searchByKeyWord',
    method: 'get',
    params
  })
}


export function getKPlayUrl(params) {
    return request({
      url: '/v2/music/getPlayUrl',
      method: 'get',
      params
    })
  }
