<template>
  <div
    id="play-bar"
    class="aplayer aplayer-withlrc aplayer-withlist"
    style="display: block"
  >
  <div v-if="musicIng.lyric[line]" class="aplayer-lyrics-container" style="display: block;">{{musicIng.lyric[line].lineLyric}}</div>
  
    <audio
      
      @timeupdate="updateTime"
      v-show="false"
      ref="audio"
      :src="musicIng.audioUrl"
    ></audio>

    <div class="aplayer-body">
      <!-- 播放器封面 -->
      <div class="aplayer-pic" :style="{ backgroundImage: this.musicIng.img }">
        <!-- aplayer-button aplayer-play -->
        <div :class="paused?  'aplayer-button aplayer-play': 'aplayer-button aplayer-pause' " @click="handlePauseOrPlay">
          <svg
            xmlns="http://www.w3.org/2000/svg"
            version="1.1"
            viewBox="0 0 17 32"
          >
          <!-- M15.552 15.168q0.448 0.32 0.448 0.832 0 0.448-0.448 0.768l-13.696 8.512q-0.768 0.512-1.312 0.192t-0.544-1.28v-16.448q0-0.96 0.544-1.28t1.312 0.192z -->
            <path
              :d="!paused? 'M14.080 4.8q2.88 0 2.88 2.048v18.24q0 2.112-2.88 2.112t-2.88-2.112v-18.24q0-2.048 2.88-2.048zM2.88 4.8q2.88 0 2.88 2.048v18.24q0 2.112-2.88 2.112t-2.88-2.112v-18.24q0-2.048 2.88-2.048z' 
              : 'M15.552 15.168q0.448 0.32 0.448 0.832 0 0.448-0.448 0.768l-13.696 8.512q-0.768 0.512-1.312 0.192t-0.544-1.28v-16.448q0-0.96 0.544-1.28t1.312 0.192z'"
            ></path>
          </svg>
        </div>
      </div>
      <!-- 播放器按钮 -->
      <div class="pb-control">
        <!-- 上一曲 -->
        <svg 
          @click="handlerInaSong"
          viewBox="0 0 1024 1024">
          <path
            d="M511.984485 977.424927a465.440441 465.440441 0 1 1 465.440442-465.440442 465.440441 465.440441 0 0 1-465.440442 465.440442z m-93.088088-620.587255h-62.058725v310.293627h62.058725v-310.293627z m248.234902 0l-217.205539 155.146813 217.205539 155.146814v-310.293627z"
          ></path>
        </svg>

        <svg
          v-show="paused"
          viewBox="0 0 1024 1024"
          style="display: none"
          @click="handlePauseOrPlay"
        >
          <path
            d="M512 1024a512 512 0 1 1 512-512 512 512 0 0 1-512 512z m148.873846-530.983385c-15.36-13.154462-195.741538-166.951385-204.8-174.592-11.421538-9.531077-22.843077 2.599385-22.843077 18.865231v349.892923c0 17.92 12.603077 26.584615 22.055385 18.707693 13.390769-10.712615 194.56-165.415385 205.587692-174.985847a27.214769 27.214769 0 0 0 0-37.888z"
          ></path>
        </svg>
        <!-- 暂停 -->
        <svg
          v-show="!paused"
          style=""
          viewBox="0 0 1024 1024"
          @click="handlePauseOrPlay"
        >
          <path
            d="M512 1024C229.23 1024 0 794.77 0 512S229.23 0 512 0s512 229.23 512 512-229.23 512-512 512zM392.533 273.066c-28.277 0-51.2 22.923-51.2 51.2v375.467c0 28.277 22.923 51.2 51.2 51.2 28.277 0 51.2-22.923 51.2-51.2V324.266c0-28.277-22.923-51.2-51.2-51.2z m238.933 0c-28.277 0-51.2 22.923-51.2 51.2v375.467c0 28.277 22.923 51.2 51.2 51.2 28.277 0 51.2-22.923 51.2-51.2V324.266c0-28.277-22.923-51.2-51.2-51.2z"
          ></path>
        </svg>
        <!-- 下一曲 -->
        <svg 
          @click="handlerNextSong"
          viewBox="0 0 1024 1024">
          <path
            d="M511.984485 977.424927a465.440441 465.440441 0 1 1 465.440442-465.440442 465.440441 465.440441 0 0 1-465.440442 465.440442z m-155.146813-620.587255v310.293627l217.205539-155.146814z m310.293627 0h-62.058725v310.293627h62.058725v-310.293627z"
          ></path>
        </svg>
      </div>
      <div class="pb-control-right">
        <button type="button" @click="handlerMuted" class="aplayer-icon pb-icon-volume-down">
          <svg
            xmlns="http://www.w3.org/2000/svg"
            version="1.1"
            viewBox="0 0 28 32"
          >
            <path v-show="muted" d="M13.728 6.272v19.456q0 0.448-0.352 0.8t-0.8 0.32-0.8-0.32l-5.952-5.952h-4.672q-0.48 0-0.8-0.352t-0.352-0.8v-6.848q0-0.48 0.352-0.8t0.8-0.352h4.672l5.952-5.952q0.32-0.32 0.8-0.32t0.8 0.32 0.352 0.8z">

            </path>
            <path v-show="!muted"
              d="M13.728 6.272v19.456q0 0.448-0.352 0.8t-0.8 0.32-0.8-0.32l-5.952-5.952h-4.672q-0.48 0-0.8-0.352t-0.352-0.8v-6.848q0-0.48 0.352-0.8t0.8-0.352h4.672l5.952-5.952q0.32-0.32 0.8-0.32t0.8 0.32 0.352 0.8zM20.576 16q0 1.344-0.768 2.528t-2.016 1.664q-0.16 0.096-0.448 0.096-0.448 0-0.8-0.32t-0.32-0.832q0-0.384 0.192-0.64t0.544-0.448 0.608-0.384 0.512-0.64 0.192-1.024-0.192-1.024-0.512-0.64-0.608-0.384-0.544-0.448-0.192-0.64q0-0.48 0.32-0.832t0.8-0.32q0.288 0 0.448 0.096 1.248 0.48 2.016 1.664t0.768 2.528z"
            ></path>
          </svg>
        </button>
        <div class="progress ap-volume-bar" ref="volumeProgerssBar" @click="clickVolume" @mouseup="handleMouseupVolume">
          <div
            v-show="!muted"
            ref="volumeProgerss"
            @mousedown="handleMousedownVolume"
            class="progress-bar ap-volume-bar-value"
            style="width: 50%"
          ></div>
        </div>
        <button type="button" class="aplayer-icon pb-icon-order">
          <svg
            xmlns="http://www.w3.org/2000/svg"
            version="1.1"
            viewBox="0 0 32 32"
          >
            <path
              d="M0.622 18.334h19.54v7.55l11.052-9.412-11.052-9.413v7.549h-19.54v3.725z"
            ></path>
          </svg></button
        ><button type="button" class="aplayer-icon pb-icon-loop">
          <svg
            xmlns="http://www.w3.org/2000/svg"
            version="1.1"
            viewBox="0 0 29 32"
          >
            <path
              d="M9.333 9.333h13.333v4l5.333-5.333-5.333-5.333v4h-16v8h2.667v-5.333zM22.667 22.667h-13.333v-4l-5.333 5.333 5.333 5.333v-4h16v-8h-2.667v5.333z"
            ></path>
          </svg></button
        ><button style="" type="button" class="aplayer-icon aplayer-icon-speed">
          1X
        </button>
      </div>
      <div class="aplayer-info">
        <div class="aplayer-music">
          <span class="aplayer-title">{{musicIng.name}}</span>
          <span class="aplayer-author">
            - {{musicIng.artist}}</span
          >
        </div>

        <div class="aplayer-controller">
          <div
            class="aplayer-bar-wrap"
            ref="progress"
            @click="clickProgress"
            @mouseup="handleMouseup"
          >
            <div class="aplayer-bar">
              <div class="aplayer-loaded" style="width: 100%"></div>
              <div
                ref="currentProgress"
                class="aplayer-played"
                style="
                  width: 0%;
                  background: #ff0099 none repeat scroll 0% 0%;
                "
              >
                <span
                  class="aplayer-thumb"
                  style="background: #ff0099"
                  @mousedown="handleMousedown"
                  ref="circle"
                >
                  <span class="aplayer-loading-icon"
                    ><svg
                      xmlns="http://www.w3.org/2000/svg"
                      version="1.1"
                      viewBox="0 0 32 32"
                    >
                      <path
                        d="M4 16c0-6.6 5.4-12 12-12s12 5.4 12 12c0 1.2-0.8 2-2 2s-2-0.8-2-2c0-4.4-3.6-8-8-8s-8 3.6-8 8 3.6 8 8 8c1.2 0 2 0.8 2 2s-0.8 2-2 2c-6.6 0-12-5.4-12-12z"
                      ></path></svg
                  ></span>
                </span>
              </div>
            </div>
          </div>
          <div class="aplayer-time">
            <span class="aplayer-time-inner">
              <span class="aplayer-ptime">{{ currentDuration }}</span> /
              <span class="aplayer-dtime">{{ duration }}</span>
            </span>
            <span class="aplayer-icon aplayer-icon-back">
              <svg
                xmlns="http://www.w3.org/2000/svg"
                version="1.1"
                viewBox="0 0 32 32"
              >
                <path
                  d="M25.468 6.947c-0.326-0.172-0.724-0.151-1.030 0.057l-6.438 4.38v-3.553c0-0.371-0.205-0.71-0.532-0.884-0.326-0.172-0.724-0.151-1.030 0.057l-12 8.164c-0.274 0.186-0.438 0.496-0.438 0.827s0.164 0.641 0.438 0.827l12 8.168c0.169 0.115 0.365 0.174 0.562 0.174 0.16 0 0.321-0.038 0.468-0.116 0.327-0.173 0.532-0.514 0.532-0.884v-3.556l6.438 4.382c0.169 0.115 0.365 0.174 0.562 0.174 0.16 0 0.321-0.038 0.468-0.116 0.327-0.173 0.532-0.514 0.532-0.884v-16.333c0-0.371-0.205-0.71-0.532-0.884z"
                ></path>
              </svg>
            </span>
            <span class="aplayer-icon aplayer-icon-play"
              ><svg
                xmlns="http://www.w3.org/2000/svg"
                version="1.1"
                viewBox="0 0 17 32"
              >
                <path
                  d="M14.080 4.8q2.88 0 2.88 2.048v18.24q0 2.112-2.88 2.112t-2.88-2.112v-18.24q0-2.048 2.88-2.048zM2.88 4.8q2.88 0 2.88 2.048v18.24q0 2.112-2.88 2.112t-2.88-2.112v-18.24q0-2.048 2.88-2.048z"
                ></path></svg
            ></span>
            <span class="aplayer-icon aplayer-icon-forward">
              <svg
                xmlns="http://www.w3.org/2000/svg"
                version="1.1"
                viewBox="0 0 32 32"
              >
                <path
                  d="M25.468 6.947c-0.326-0.172-0.724-0.151-1.030 0.057l-6.438 4.38v-3.553c0-0.371-0.205-0.71-0.532-0.884-0.326-0.172-0.724-0.151-1.030 0.057l-12 8.164c-0.274 0.186-0.438 0.496-0.438 0.827s0.164 0.641 0.438 0.827l12 8.168c0.169 0.115 0.365 0.174 0.562 0.174 0.16 0 0.321-0.038 0.468-0.116 0.327-0.173 0.532-0.514 0.532-0.884v-3.556l6.438 4.382c0.169 0.115 0.365 0.174 0.562 0.174 0.16 0 0.321-0.038 0.468-0.116 0.327-0.173 0.532-0.514 0.532-0.884v-16.333c0-0.371-0.205-0.71-0.532-0.884z"
                ></path>
              </svg>
            </span>
            <div class="aplayer-volume-wrap">
              <button
                type="button"
                class="aplayer-icon aplayer-icon-volume-down"
              >
                <svg
                  xmlns="http://www.w3.org/2000/svg"
                  version="1.1"
                  viewBox="0 0 28 32"
                >
                  <path
                    d="M13.728 6.272v19.456q0 0.448-0.352 0.8t-0.8 0.32-0.8-0.32l-5.952-5.952h-4.672q-0.48 0-0.8-0.352t-0.352-0.8v-6.848q0-0.48 0.352-0.8t0.8-0.352h4.672l5.952-5.952q0.32-0.32 0.8-0.32t0.8 0.32 0.352 0.8zM20.576 16q0 1.344-0.768 2.528t-2.016 1.664q-0.16 0.096-0.448 0.096-0.448 0-0.8-0.32t-0.32-0.832q0-0.384 0.192-0.64t0.544-0.448 0.608-0.384 0.512-0.64 0.192-1.024-0.192-1.024-0.512-0.64-0.608-0.384-0.544-0.448-0.192-0.64q0-0.48 0.32-0.832t0.8-0.32q0.288 0 0.448 0.096 1.248 0.48 2.016 1.664t0.768 2.528z"
                  ></path>
                </svg>
              </button>
              <div class="aplayer-volume-bar-wrap">
                <div class="aplayer-volume-bar">
                  <div
                    class="aplayer-volume"
                    style="
                      height: 76.6667%;
                      background: rgb(254, 68, 75) none repeat scroll 0% 0%;
                    "
                  ></div>
                </div>
              </div>
            </div>
            <button
              type="button"
              class="aplayer-icon aplayer-icon-order"
            >
              <svg xmlns="http://www.w3.org/2000/svg" version="1.1" viewBox="0 0 32 32">
                  <path d="M0.622 18.334h19.54v7.55l11.052-9.412-11.052-9.413v7.549h-19.54v3.725z">
                  </path>
              </svg>

            </button>
            <button
              type="button"
              class="aplayer-icon aplayer-icon-loop"
            >
              <svg xmlns="http://www.w3.org/2000/svg" version="1.1" viewBox="0 0 29 32">
                  <path
                      d="M9.333 9.333h13.333v4l5.333-5.333-5.333-5.333v4h-16v8h2.667v-5.333zM22.667 22.667h-13.333v-4l-5.333 5.333 5.333 5.333v-4h16v-8h-2.667v5.333z">
                  </path>
              </svg>
            
            </button
            ><button
              style="width: auto; margin-left: 2px"
              type="button"
              class="aplayer-icon aplayer-icon-speed"
            >
              1X
            </button>
            <button
              type="button"
              class="aplayer-icon aplayer-icon-menu"
            ></button>
            <button
              type="button"
              class="aplayer-icon aplayer-icon-lrc"
            ></button>
          </div>
        </div>
      </div>
      <div class="aplayer-notice"></div>
      <div class="aplayer-miniswitcher">
        <button class="aplayer-icon"></button>
      </div>
    </div>
  </div>
</template>

<script>
// import { getPlayUrl } from "@/api/kuwo";

export default {
  name: "PlayMusic",
  computed: {
    // ...mapGetters(["img", "musicListIndex", "musicList"]),
  },
  // props: ['playUrlList','handlerNextSong','handlerInaSong'],
  props: {
    musicIng:{
      type:Object,
    },
    handlerInaSong: {
      type: Function,
      default: () => {
        return Function;
      },
    },
    handlerNextSong:{
      type: Function,
      default: () => {
        return Function;
      },
    },
  },
  
  data() {
    return {
      duration: "00:00",
      currentDuration: "00:00",
      audio: "",
      paused: true,
      isMoveIn: false, //是否在移动中
      muted:false,
      line:0
    };
  },
  methods: {
    handlerMuted(){
      this.audio.muted = !this.audio.muted
      this.muted = !this.muted
    },

    //切歌
    nextSong(){
      setTimeout(() => {
        this.$refs.currentProgress.style.width = "0px";
        this.paused = true;
        this.audio.play()
      }, 100);
    },

    handlePauseOrPlay() {
      this.audio.paused ? this.audio.play() : this.audio.pause();
      this.paused = !this.paused;
    },

    getDuration() {
      this.duration = this.timeFormat(this.$refs.audio.duration)
      this.audio = this.$refs.audio
    },

    updateProgress(MoveX) {
      //当前移动的位置 = 当前移动的位置 / 当前进度条的可视长度    //this.$refs.progress.clientWidth 注意一定要拿总长度 否则会拿进度条已经走过的长度
      let clickProgress = MoveX / this.$refs.progress.clientWidth;
      //设置播放的时间 = 总时长 * 当前点击的长度
      this.audio.currentTime = this.audio.duration * clickProgress;
      //设置移动的位置
      this.$refs.currentProgress.style.width = MoveX + "px";
      // this.$refs.circle.style.left =
        // MoveX - this.$refs.circle.clientWidth / 2 + "px";
    },
    updatelyric(){
      let list = this.musicIng.lyric;
      let currnetTime = Number(this.audio.currentTime)
      for (let i = 0;i<list.length ;i++) {
        if (list[i].time-0.3 < currnetTime) {
          this.line = i;
        }
      }
    },

    updateTime() {
      if (!this.$refs.progress) return;
      this.currentDuration = this.timeFormat(this.audio.currentTime);
      //如果不是正在移动 和 没有暂停播放就执行
      if (!this.isMoveIn || !this.audio.paused) {
        // 设置当前时间
        let MoveX =
          this.$refs.progress.clientWidth *
          (this.audio.currentTime / this.audio.duration);
        //播放时更新距离
        this.$refs.currentProgress.style.width = MoveX + "px";
        // this.$refs.circle.style.left =
          // MoveX - this.$refs.circle.clientWidth / 2 + "px";
        if (this.musicIng.lyric.length > 0) this.updatelyric(this.audio.currentTime)
        if (!this.audio.paused) {
          this.paused = false;
        }
      }
    },

    /**
     * 音频播放时间换算
     * @param {number} value - 音频当前播放时间，单位秒
     */
    //将单位为秒的的时间转换成mm:ss的形式
    timeFormat(number) {
      let minute = parseInt(number / 60);
      let second = parseInt(number % 60);
      minute = minute >= 10 ? minute : "0" + minute;
      second = second >= 10 ? second : "0" + second;
      return minute + ":" + second;
    },

    fetch() {
      this.getDuration()
      let that = this;
      var myVid = this.audio;
      myVid.loop = false;
      // 监听音频播放完毕
      myVid.addEventListener(
        "ended",
        function () {
          // 进度条初始化
          that.$refs.currentProgress.style.width = "0px";
          that.handlerNextSong()
        },
        false
      );
      if (myVid != null) {
        myVid.oncanplay = function () {
          that.duration = that.timeFormat(myVid.duration); // 计算音频时长
        };
        myVid.volume = 0.5; // 设置音量50%
      }
    },
    clickProgress(e) {
      //如果不是正在移动 和 没有暂停播放就执行
      if (!this.isMoveIn || !this.audio.paused) {
        this.updateProgress(e.offsetX);
      }
    },
    //鼠标弹起
    handleMouseup() {
      setTimeout(() => {
        this.audio.play();
        this.paused = false;
        this.isMoveIn = false;
      }, 150);
    },
    //小圆圈按下
    handleMousedown() {
      this.audio.pause();
      this.paused = true;
      this.isMoveIn = true;
      let progress = this.$refs.progress;
      //进度条 左 边距离页面左边的距离 移动最小值
      let moveMin = progress.offsetParent.offsetLeft + progress.offsetLeft;
      //进度条 右 边距离页面左边的距离 移动最大值
      let moveMax =
        progress.offsetParent.offsetLeft +
        progress.offsetLeft +
        progress.clientWidth;
      //小圆圈的宽度
      // let circleWidth = this.$refs.circle.clientWidth / 2;
      let move = (move) => {
        if (move.pageX >= moveMax) {
          return;
        } else if (move.pageX <= moveMin) {
          return;
        }
        // this.$refs.circle.style.left =
          // move.pageX - moveMin - circleWidth + "px";
        this.updateProgress(move.pageX - moveMin);
      };
      //获取当前鼠标的位置 X
      document.addEventListener("mousemove", move);
      //鼠标弹起来
      document.addEventListener("mouseup", () => {
        document.removeEventListener("mousemove", move);
      });
    },
    clickVolume(e) {
      this.updateVolume(e.offsetX);
    },
    //鼠标弹起
    handleMouseupVolume() {
    },
    //小圆圈按下
    handleMousedownVolume() {
      let progress = this.$refs.volumeProgerssBar;
      let moveMin = progress.offsetParent.offsetLeft + progress.offsetLeft;
      let moveMax =
        progress.offsetParent.offsetLeft +
        progress.offsetLeft +
        progress.clientWidth;
      let move = (move) => {
        if (move.pageX >= moveMax) {
          return;
        } else if (move.pageX <= moveMin) {
          return;
        }
        this.updateVolume(move.pageX - moveMin);
      };
      //获取当前鼠标的位置 X
      document.addEventListener("mousemove", move);
      //鼠标弹起来
      document.addEventListener("mouseup", () => {
        document.removeEventListener("mousemove", move);
      });
    },
    updateVolume(MoveX) {
      //当前移动的位置 = 当前移动的位置 / 当前进度条的可视长度    //this.$refs.progress.clientWidth 注意一定要拿总长度 否则会拿进度条已经走过的长度
      let clickProgress = MoveX / this.$refs.volumeProgerssBar.clientWidth;
      this.audio.volume = clickProgress
      //设置移动的位置
      this.$refs.volumeProgerss.style.width = MoveX + "px";
      // this.$refs.circle.style.left =
        // MoveX - this.$refs.circle.clientWidth / 2 + "px";
    },
  },
  mounted() {
    this.fetch();
  },
   watch: {
      musicIng:{ //深度监听，可监听到对象、数组的变化
          handler () {
            this.nextSong()
          },
          deep:true
      }
    },
  
};
</script>

<style>
</style>