import request from '@/utils/request'

// 模糊搜索
export function getMbyKeyword(params) {
    return request({
        url: '/v3/music/searchByKeyword',
        method: 'get',
        params
    })
}

export function getMLyric(params) {
    return request({
        url: '/v3/music/getLyric',
        method: 'get',
        params
    })
}