import request from '@/utils/request'

// 获取推荐列表
export function getMusicList(params) {
  return request({
    url: '/v1/music/getMusicList',
    method: 'get',
    params
  })
}
//获取歌词的url
export function getPlayUrl(params) {
  return request({
    url: '/v1/music/getPlayUrl',
    method: 'get',
    params
  })
}

