<template>
  <div>
    <div class="nav-bar">
      <div class="navBar-logo">Acoustic<span>Enjoy</span></div>
      <div>
        <a class="navBar-group head"><span>BROWSE</span></a>
        <a
          @click="selectIndex = 1"
          :class="
            selectIndex == 1 ? 'navBar-group navBar-current' : 'navBar-group'
          "
        >
          <i class="iconfont icon-sousuo"></i><span>搜索</span>
        </a>
        <a
          @click="selectIndex = 2"
          :class="
            selectIndex == 2 ? 'navBar-group navBar-current' : 'navBar-group'
          "
        >
          <i class="iconfont icon-fm"></i><span>播放列表</span>
        </a>
        <a class="navBar-group head"><span>PAGES</span> </a>
        <a
          @click="selectIndex = 3"
          :class="
            selectIndex == 3 ? 'navBar-group navBar-current' : 'navBar-group'
          "
        >
          <i class="iconfont icon-diantai"></i><span>广播电台</span>
        </a>
        <a
          @click="selectIndex = 4 "
          :class="
            selectIndex == 4 ? 'navBar-group navBar-current' : 'navBar-group'
          "
        >
          <i class="iconfont icon-jiesuo"></i><span>解锁音乐</span>
        </a>
        <a class="navBar-group head"><span>OTHER</span></a>
        <a
          @click="selectIndex = 5"
          :class="
            selectIndex == 5 ? 'navBar-group navBar-current' : 'navBar-group'
          "
        >
          <span>在线工具</span>
        </a>
      </div>
    </div>
    <div class="main-view">
      <nav
        class="navbar border-bottom sticky-top navbar-light bg-white px-md-4"
      >
        <div class="navbar-nav mr-auto pr-2">
          <div class="d-md-none">AcousticEnjoy</div>
        </div>
        <a
          class="nav-link d-md-block d-none"
          data-toggle="modal"
          data-target="#shortcuts"
        >
          <i class="iconfont icon-kuaijiejian"></i>
        </a>
        <a class="nav-link" data-toggle="modal" data-target="#configure"
          ><i class="iconfont icon-shezhi"></i
        ></a>
        <li class="nav-item dropdown">
          <a
            class="nav-link dropdown-toggle"
            href="#"
            id="langdrop"
            role="button"
            data-toggle="dropdown"
            aria-haspopup="true"
            aria-expanded="false"
            >回到顶部</a
          >
          <div
            id="lang"
            class="dropdown-menu"
            aria-labelledby="langdrop"
            style="right: 0; left: auto"
          >
            <button class="dropdown-item" data-lang="en-us">English</button
            ><button class="dropdown-item" data-lang="zh-cn">简体中文</button
            ><button class="dropdown-item" data-lang="zh-tw">繁體中文</button>
          </div>
        </li>
      </nav>
      <!-- 搜索页面 -->
      <search-audio
        v-show="selectIndex === 1 "
        :songList='songList'
        :searchHandler='searchHandler'
        ></search-audio>
        
      <!-- 歌曲列表 -->
      <music-list v-show="selectIndex === 2"
        :musicList="musicList"
        :musicListIndex="musicListIndex"
        :musicIng="musicIng"
        :getMoreMusic="getMoreMusic"
        :palyHandler="palyHandler"
        :isOpen="isOpen"
      ></music-list>


      <div v-show="selectIndex === 6"  id="listPage" class="container-xl" style="display: block;">
      <div class="home-title">我的歌单（<span class="num">0</span>）</div>
      <div id="mySongSheet" class="listwrapper">
        <div class="container-fluid">
          <ul class="row row-cols-2 row-cols-sm-4 row-cols-md-5">
            <li class="col importList">
              <div class="lw-img"><img
                  src="http://singerimg.kugou.com/uploadpic/softhead/%7Bsize%7D/20180906/20180906181115284.jpg">
              </div>
              <div class="lw-title">导入我的歌单列表
              </div>
            </li>
          </ul>
        </div>
      </div>
      <div class="home-title">我的收藏（<span class="num">0</span>）</div>
      <div>功能开发中，暂不支持使用</div>
      <div id="mySongCollect" class="listwrapper"></div>
    </div>






    </div>
      <!-- 播放器 -->
      <play-music v-show="musicIng.audioUrl"
        :musicIng="musicIng" 
        :handlerInaSong="handlerInaSong"
        :handlerNextSong='handlerNextSong'></play-music>  
      <!-- 移动端菜单适配 -->
     <div class="m-nav-bar">
      <ul>
        <li
          @click="selectIndex = 1"
          :class="
            selectIndex == 1
              ? 'm-navBar-group m-navBar-current'
              : 'm-navBar-group'
          "
        >
        <i class="iconfont icon-sousuo"></i>
        </li>
        <li
          @click="selectIndex = 2"
          :class="
            selectIndex == 2
              ? 'm-navBar-group m-navBar-current'
              : 'm-navBar-group'
          "
        >
          <i class="iconfont icon-fm"></i>
        </li>
        <li
          @click="selectIndex = 6"
          :class="
            selectIndex == 6
              ? 'm-navBar-group m-navBar-current'
              : 'm-navBar-group'
          "
        >
          <i class="iconfont icon-like"></i>
        </li>
      </ul>
    </div>
    
  </div>
</template>

<script>
import { getMusicList,getPlayUrl,searchMusicByKeyWord,getRcmPlayList,getPlayListInfo } from "@/api/kuwo";
import { getKbyKeyword,getKPlayUrl } from "@/api/kugou";
import { getMbyKeyword,getMLyric } from "@/api/migu";
import { getCbyKeyword } from "@/api/cloudMusic";
import PlayMusic from '@/components/PlayMusic/PlayMusic.vue'
import SearchAudio from '@/components/SearchAudio/SearchAudio.vue'
import MusicList from '@/components/MusicList/MusicList.vue'

export default {
  name: "HomePage",
  components:{
    PlayMusic,
    SearchAudio,
    MusicList
  },
  props:{
    isOpen:{
        type:Function,
        default: () => {
        return Function;
      },
    }
  },
  data() {
    return {
      // 导航
      selectIndex: 1,
      selectValue: ["搜索", "播放列表", "广播电台", "解锁音乐", "在线工具"],

      // 歌曲列表
      musicList: [],
      musicListIndex:0,
      menu:0,
      listValue:{
          str:93,
          pn:0,
          rn:10,
      },

      musicIng:{
        img:'url("http://singerimg.kugou.com/uploadpic/softhead/%7Bsize%7D/20180906/20180906181115284.jpg")',
        name:'',
        rid:'',
        artist:'',
        audioUrl:'',
        lyric:[],
      },
      
      songList:[],
    };
  },
  computed: {},
  methods: {
    //上一曲
    handlerInaSong(){
      let index = this.musicListIndex-1
      if (index < 0) {
        this.musicListIndex = this.musicList.length - 1
      }else{
        this.musicListIndex = index
      }
      this.getUrl()
    },

    // 下一曲
    handlerNextSong(){
      let index = this.musicListIndex+1
      if (index > this.musicList.length - 1) {
        this.musicListIndex = 0
      }else{
        this.musicListIndex = index
      }
      this.getUrl()
    },
    setImgUrl(img){
      let imgUrl = 'url('+img+')';
      return imgUrl
    },

    // 下一页
    getMoreMusic(){
        this.listValue.pn++;
        switch(this.menu){
          case 0 : this.getMusics();break;
          case 1 : this.getByKeyWord();break;
          case 2 : this.getPlayListInfo();break;
          case 3 : this.kgsearchByKeyword(); break;
          case 4 : this.miGuByKeyword(); break;
          case 5 : this.cloudMusicByKeyword();break;

        }
    },
    
    getUrl(){
        switch(this.menu){
          case 0 : this.getPlayVudioUrl();break;
          case 1 : this.getPlayVudioUrl();break;
          case 2 : this.getPlayVudioUrl();break;
          case 3 : this.kggetPlayUrl(); break;
          case 4 : this.miGuPlayUrl(); break;
          case 5 : this.cloudMusicPlayUrl();break;
        }

    },
    // 根据关键词搜索处理
    searchHandler(menu,value){
      this.menu = menu
      this.listValue = value
      this.musicList = []
      this.getMoreMusic()
      this.selectIndex = 2
      setTimeout(()=>{
        this.palyHandler(0)
      },1000)
    },

    // 播放歌曲处理
    palyHandler(index){
        this.musicListIndex=index
        this.getUrl()
    },

    // 网易云音乐播放处理
    cloudMusicPlayUrl(){
      const music = this.musicList[this.musicListIndex]
      this.musicIng.name = music.name;
      this.musicIng.artist = music.artist
      this.musicIng.img = this.setImgUrl(music.pic)
      this.musicIng.audioUrl = "http://music.163.com/song/media/outer/url?id="+music.rid+".mp3"
      this.musicIng.lyric=[]
    },

    // 网易云音乐模糊搜索
    async cloudMusicByKeyword(){
      let list = this.musicList
      await getCbyKeyword(this.listValue).then(response =>{
      this.musicList = list.concat(response.data);
      this.musicIng.img = this.setImgUrl(this.musicList[this.musicListIndex].pic);
      }).catch(error=>{
        console.log(error)
      })

    },

    // 咪咕播放处理
    async miGuPlayUrl(){
      const music = this.musicList[this.musicListIndex]
      this.musicIng.name = music.name;
      this.musicIng.artist = music.artist
      this.musicIng.img = this.setImgUrl(music.albumPic)
      this.musicIng.audioUrl = "http://app.pd.nf.migu.cn/MIGUM2.0/v1.0/content/sub/listenSong.do?toneFlag=HQ&netType=00&userId=15548614588710179085069&ua=Android_migu&version=5.1&copyrightId="+music.copyrightId+"&contentId="+music.rid+"&resourceType=2&channel=0"
      await getMLyric({'copyrightId' :music.copyrightId }).then(response=>{
        this.musicIng.lyric = response.data
      }).catch(error=>{ 
        console.log(error);
      })
    },


    //咪咕模糊搜索
    async miGuByKeyword(){
      let list = this.musicList
      await getMbyKeyword(this.listValue).then(response =>{
        this.musicList = list.concat(response.data);
        this.musicIng.img = this.setImgUrl(this.musicList[this.musicListIndex].albumPic);
      }).catch(error=>{
        console.log(error)
      })
    },

    //酷狗音乐获取url
    async  kggetPlayUrl() {
      const music = this.musicList[this.musicListIndex]
      const musicId = {musicId:music.rid};
      await getKPlayUrl(musicId).then((response) => {
          this.musicIng.name = music.name;
          this.musicIng.artist = music.artist
          this.musicIng.img = this.setImgUrl(response.data.img)
          this.musicIng.audioUrl = response.data.url;
          this.musicIng.lyric = response.data.lyric;
        })
        .catch((error) => {
          console.log(error);
        });
    },

    // 酷狗音乐模糊搜索
    async kgsearchByKeyword(){
      let list = this.musicList
      await getKbyKeyword(this.listValue).then(response =>{
        this.musicList = list.concat(response.data);
        if (!this.musicIng.img) {
          this.musicIng.img = this.setImgUrl("http://singerimg.kugou.com/uploadpic/softhead/%7Bsize%7D/20180906/20180906181115284.jpg");
        }
      }).catch(error=>{
        console.log(error)
      })
    },


    // 获取酷我首页推荐歌单
    async getMusics(){
        await getMusicList(this.listValue).then(response=>{
        let list = this.musicList
        this.musicList = list.concat(response.data.musicList);
        this.musicIng.img = this.setImgUrl(this.musicList[this.musicListIndex].albumPic)
    }).catch(error=>{
        console.log(error);
    })
    },

    // 获取推荐歌单歌曲·
    async getPlayListInfo(){
      let list = this.musicList
      await getPlayListInfo(this.listValue).then(response=>{
        this.musicList = list.concat(response.data);
        this.musicIng.img = this.setImgUrl(this.musicList[this.musicListIndex].albumPic)
      }).catch(error=>{
        console.log(error)
      })
    },

    // 根据关键词获取歌曲列表
    async getByKeyWord(){
      let list = this.musicList
      await searchMusicByKeyWord(this.listValue).then(response =>{
        this.musicList = list.concat(response.data);
        this.musicIng.img = this.setImgUrl(this.musicList[this.musicListIndex].albumPic)
      }).catch(error=>{
        console.log(error)
      })
    },
    
    // 根据musicId获取url和歌词信息
    async  getPlayVudioUrl() {
      const music = this.musicList[this.musicListIndex]
      const musicId = {musicId:music.rid};
      await
        getPlayUrl(musicId).then((response) => {
          this.musicIng.name = music.name;
          this.musicIng.artist = music.artist
          if (music.albumPic) {
            this.musicIng.img = this.setImgUrl(music.albumPic)
          }
          this.musicIng.audioUrl = response.data.url;
          this.musicIng.lyric = response.data.lyric;
        })
        .catch((error) => {
          console.log(error);
          // this.handlerNextSong()
        });
    },

    // 获取主页推荐
    async getSongLists(){
      await getRcmPlayList({str:"hot",pn:1,rn:10,}).then(response=>{
        this.songList = response.data
      }).catch(error=>{
        console.log(error)
      })
    },
  },


  created(){
    if (sessionStorage.getItem('listValue')) {
        this.listValue.str = JSON.parse(sessionStorage.getItem("listValue"));
        this.listValue.pn = 0
        this.menu = Number(sessionStorage.getItem("menu"))
        this.selectIndex = Number(sessionStorage.getItem("selectIndex"))
        setTimeout(()=>{
          this.getMoreMusic()
        },500)
        
    }
    this.getSongLists();

    window.addEventListener("beforeunload",()=>{
      sessionStorage.setItem("listValue",JSON.stringify(this.listValue.str))
      sessionStorage.setItem("menu",JSON.stringify(this.menu))
      sessionStorage.setItem("selectIndex",JSON.stringify(this.selectIndex))
    })
    //定时自动播放
    // setTimeout(()=>{
    //   this.palyHandler(0)
    // },1000)

  },
  watch: {
    selectIndex: function () {
      document.title = this.selectValue[this.selectIndex - 1] + " - Acoustic Enjoyment - 听觉享受";
    },
    'musicIng.name':function(){
      document.title = this.musicIng.name +" - "+  this.musicIng.artist + " - Acoustic Enjoyment - 听觉享受";
    }
  },
};
</script>
