import axios from 'axios'
import {aes} from '@/utils/crypt';
import { myMessage } from '@/utils/message'


// create an axios instance
const service = axios.create({
  // baseURL:'http://localhost:8089', // url = base url + request url
  // withCredentials: true, // send cookies when cross-domain requests
  timeout: 5000 // request timeout
})

// 请求拦截器
service.interceptors.request.use(
  config => {
    let timestamp = new Date().getTime().toString().substring(3);
    let siger = aes.en(timestamp)
    config.headers["siger"] = siger
    return config
  },
  error => {
    // do something with request error
    console.log(error) // for debug
    return Promise.reject(error)
  }
)

// 响应拦截器
service.interceptors.response.use(

  response => {
    const res = response.data
    // console.log(res)
    // if the custom code is not 20000, it is judged as an error.
    if (res.code !== 200) {
      let message;
      let type;
      if (res.code === -1) {
        message = '无更多数据，请勿重复点击'
        type = 'warning'
      }
      if (res.code === 401) {
        message = '网络异常，请稍后再试'
        type = 'error'
      }
      myMessage({
        message: message || res.message || 'error',
        type: type || "warning",
      });
    return Promise.reject(new Error(res.message || 'Error'))
    } else {
      return res
    }
  },
  error => {
    console.log('err' + error) // for debug
    myMessage({
      message: error.message === 'Network Error' ? '连接超时，请稍后重试' : 'error.message',
      type: 'error'
    });
    return Promise.reject(error)
  }
)

export default service
