                <a class="btn btn-outline-secondary copy" ><a/>
                  <i class="iconfont icon-fuzhi"></i></a>
                <a
                  class="
                    btn btn-outline-secondary
                    dropdown-toggle dropdown-toggle-split
                  "
                  data-toggle="dropdown"
                  aria-haspopup="true"
                  aria-expanded="false"
                  href="javascript:;"
                ></a>
              </div>
            </div>
            <div class="input-group mb-2">
              <div class="input-group-prepend">
                <label class="input-group-text" for="inputGroupSelect01"
                  >FLAC</label
                >
              </div>
              <input
                v-model="info.flac"
                readonly="readonly"
                type="text"
                class="form-control"
                id="url_flac"
              />
              <div class="input-group-append">
                <a target="_blank" class="btn btn-outline-secondary download"
                  ><i class="iconfont icon-xiazai"></i></a
                ><a class="btn btn-outline-secondary copy" href="javascript:;"
                  ><i class="iconfont icon-fuzhi"></i></a
                ><a
                  class="
                    btn btn-outline-secondary
                    dropdown-toggle dropdown-toggle-split
                  "
                  data-toggle="dropdown"
                  aria-haspopup="true"
                  aria-expanded="false"
                  href="javascript:;"
                ></a>
                <div class="dropdown-menu">
                  <a class="dropdown-item qrcode" href="javascript:;"
                    >二维码下载</a
                  >
                </div>
              </div>
