              @click="isOpen(false, {})"
            >
              关闭
            </button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    musicInfo: {
      type: Object,
    },
    isOpen: {
      type: Function,
      default: () => {
        return Function;
      },
    },
  },
  data(){
    return {
      info:{
        name:'未知',
        artist:'未知',
        audioUrl:'暂无资源',
        flac:'暂无资源',
        ape:'暂无资源',
        hires:'暂无资源',
      }
    }
  },
  watch: {
      musicInfo:{
          handler (newVal) {
            if (newVal.name)this.info.name = newVal.name
            if (newVal.artist)this.info.artist = newVal.artist
            if (newVal.audioUrl)this.info.audioUrl = newVal.audioUrl
            if (newVal.flac) this.info.flac = newVal.flac
            if (newVal.ape) this.info.ape = newVal.ape
            if (newVal.hires) this.info.hires = newVal.hires

          },
          deep:true
      }
    },
};
</script>

