<template>
  <!-- 歌曲列表 -->
      <div id="audioPage" class="container-xl" style="display: block">
        <div class="right-side">
          <div class="right-row">
            <div class="TLH_cover">
              <div class="cover-art">
                <div
                  class="cover-art-image"
                  :style="{ backgroundImage: this.musicIng.img }"
                ></div>
              </div>
            </div>
            <div v-if="musicList.length > 0" class="TLH_body">
              <div class="mo-info">
                <div class="mo-info-name">
                  {{ musicList[musicListIndex].name }}
                </div>
              </div>
              <span class="mo-info-by">作者</span>
              <div class="mo-meta">{{ musicList[musicListIndex].artist }}</div>
              <div class="play-lrc aplayer aplayer-withlrc">
                <div class="aplayer-lrc">
                  <div
                    class="aplayer-lrc-contents"
                    style="transform: translateY(-160px)"
                  ></div>
                </div>
              </div>
              <div class="TLH_button">
                <button class="btn-green TLH_download" @click="download">详情</button>
                <button class="btn-green TLH_batch btn-green-disabled">
                  歌手详情
                </button>
              </div>
            </div>
          </div>
        </div>
        <div v-show="musicList.length <= 0" class="EmptyState" style="display: none">
          <i class="iconfont icon-kong"></i>
          <h1>这里是空的喔</h1>
        </div>
        <div v-show="musicList.length > 0" class="left-side" style="display: block">
          <div class="left-row">
            <div id="player" class="aplayer aplayer-withlrc aplayer-withlist">
              <div class="aplayer-list" style="max-height: 250px">
                <ol style="max-height: 250px">
                  <li
                    v-for="(item, index) in musicList"
                    :key="index"
                    @click="palyHandler(index)"
                    :class="
                    musicListIndex == index ? 'init aplayer-list-light' : 'init'
                    "
                  >
                    <!-- <span
                      @click.stop="isOpen(true,musicIng)"
                      class="aplayer-list-download iconfont icon-xiazai"
                    ></span> -->
                    <span
                      class="aplayer-list-cur"
                      style="background-color: #ff0099"
                    ></span>
                    <span class="aplayer-list-index">{{ index + 1 }}</span>
                    <span class="aplayer-list-title">{{ item.name }}</span>
                    <span class="aplayer-list-author">{{ item.artist }}</span>
                  </li>
                </ol>
              </div>
              <div class="aplayer-more" @click="getMoreMusic">下一页</div>
            </div>
          </div>
        </div>
      </div>
</template>

<script>

export default {
props: {
    musicIng:{
       type:Object,
    },
    musicList:{
       type:Array,
    },
    musicListIndex:{
        type:Number,
    },
    getMoreMusic:{
        type:Function,
        default: () => {
        return Function;
      },
    },
    palyHandler:{
        type:Function,
        default: () => {
        return Function;
      },
    },
    isOpen:{
        type:Function,
        default: () => {
        return Function;
      },
    }
},
methods:{
  download(){
    let value = Object.assign({},this.musicIng)
    this.isOpen(true,value)
  }
}



}
</script>

